import { useState, useEffect } from "react";
import axios from "axios";
import { SERVER_URI } from "../utils";

function Skills() {
  const [skill, setSkill] = useState([]);
  useEffect(() => {
    axios.get(`${SERVER_URI}/api/skill`).then((res) => {
        setSkill(res.data);
    });
  }, []);
    return (
<section className="section bg-light">
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-12 text-center">
                        <div className="container-title text-center">
                            <div className="titles">
<p className="text-capitalize mb-4">Work Expertise &amp; Technologies I Know</p>
                            </div>
                        </div>
                    </div>
                </div>
<div className="row">
<div className="col">
<div className="card explore-feature border-0 text-center bg-light p-3">
<div className="card-body p-0">
{skill.map((skill) => 
    <div key={skill.id} className="skill-icon rounded-circle shadow-lg d-inline-block" title={skill.title}>
        <img src={skill.img} alt={skill.title} className="img-fluid mx-auto" />
    </div>
)}
</div>
</div>
</div>
</div>
</div>
</section>
    );
}

export default Skills;