import { useState, useEffect } from "react";
import axios from "axios";
import { SERVER_URI } from "../utils";

function FooterSocialID() {
    const [fSocialID, setFSocialID] = useState([]);
    
useEffect(() => {
    axios.get(`${SERVER_URI}/api/social_url`).then((res) => {
        setFSocialID(res.data);
    });
}, []);
    return (
    <>
    {fSocialID.map((fsid) => {
        return(
            <li key={fsid.id} className="list-inline-item me-1">
              <a href={fsid.url} className="rounded-circle" target="_blank">
                <i className={`fab fa-${fsid.platform}`}></i>
              </a>
            </li>
            );
    })}
    </>
    );
}

export default FooterSocialID;